<template lang="pug">

div
	Title(t="Calendario académico" i="calendar")
	transition(name="slide-fade" mode="out-in")
		.container-card(v-if="!$store.state.loading")
			.cal-acad
				.refs
					.ref.ref1b 
						span Cursado: 
						.triangle.inicio
						span Inicio
						.triangle.fin
						span Fin
					.ref.ref3 Turno Ordinario
					.ref.ref4 Mesa Excepcional
					.ref.ref5 Mesa Especial
					.ref.ref6 Mesa Extraordinaria
					.ref.ref7 Receso Invernal
					.ref.ref8 Feriado
					//-.ref.ref9 Elecciones CEI
				.month(v-for="(d,i) in offsets")
					Mes( :m="i" y="19" :o="d" :r="data[i]" )

</template>
<script>
import axios from 'axios'

import Title from '@/components/Title.vue'
import Mes from '@/components/Mes.vue'

export default {
	name: 'Calendario',
	components: {
		Title,
		Mes
	},
	data() {
		return{
			data: {},
			offsets: [ 3, 6, 0, 3, 5, 1, 3, 6, 2, 4, 0, 2, 5, 1, 1 ]
		}
	},
	beforeMount(){
		this.$store.commit('startLoading')
	},
	mounted() {
		axios
			.get("/assets/json/calendario/data.json")
			.then(response => (this.data = response.data, this.$store.commit('stopLoading')))

	}
}
</script>
<style lang="stylus">

@import "../assets/styles/variables.styl";

	.cal-acad

		.refs
			.ref
				height: 24px
				line-height: 24px
				text-align: left
				padding: 0 .5rem
				letter-spacing: -1px
			.spacer
				opacity: 0
			width: 10.5rem
			height: 12rem
			color: color-white
		
		.month, .refs
			display: inline-block
			vertical-align: top
			margin: 0 12px
			.ref1b
				background: color-main
				.triangle
					width: .66rem;
					height: .66rem;
					display:inline-block
					margin: 0 .25rem 0 .33rem
					&.inicio
						background: linear-gradient(135deg, color-white 50%, transparent 50%)
					&.fin
						
						background: linear-gradient(135deg, transparent 50%, color-white 50%)

			.ref1
				background: linear-gradient(135deg, color-main 25%, transparent 25%)
			.ref2
				background: linear-gradient(135deg, transparent 75%, color-main 75%)
			.ref1, .ref2
				color: color-main !important
			.ref3
				background: #0FA400;
			.ref4
				background: #A6C70A
			.ref5
				background: #F7CE0D
			.ref6
				background: #DB7803
			.ref7
				background: #11DCE9
			.ref8
				background: rgba(#7F7F7F, 0.25)
				color: #E02323 !important
			.ref9
				background: color-main
			[class^="ref"]
				color: color-white
				text-shadow: 0 1px 1px rgba(#000, 0.1)
		
		margin: 0 -.75rem
		text-align: center

</style>