<template lang="pug">

	table.month-table
		tr.name
			th.text-main(colspan=7 v-if="m<12") {{months[m]}} '{{year}}
			th.text-main(colspan=7 v-else) {{months[m-12]}} '{{year+1}}
		tr.day-refs
			th.text-main(v-for="day in days") {{day}}
		tr(v-for="w in 6").days
			td.day.text-ink(v-for="d in 7" :class="{ 'weekend' : d == 1 || d == 7 }")
				span(v-if="(n = dayN(w,d,o)) > 0 && n <= lengths[m] && r[n-1] > 0" :class="'ref'+r[n-1]") {{n}}
				span(v-else-if="n > 0 && n <= lengths[m] ") {{n}}

</template>
<script>

export default {
  name: 'Mes',
  props: ['m','y','o','r'],
  data() {
  	return{
  		year: 20,
  		days: ['D','L','M','M','J','V','S'],
  		months: ['Enero','Febrero','Marzo','Abril','Mayo','Junio','Julio','Agosto','Septiembre','Octubre','Noviembre','Diciembre'],
  		lengths: [31,29,31,30,31,30,31,31,30,31,30,31,31,28,31,30,31]
  	}
  },
  methods:{
  	dayN: function(w, d, o){ return -o+d+(w-1)*7 }
  },
}
</script>
<style lang="stylus">

@import "../assets/styles/variables.styl";

.month-table
	td,th
		width: 1.5rem
		height: 1.5rem
		line-height: 1.5rem
		text-align:center
	.name th, .day-refs th
		text-align:center
	.day-refs th
		font-weight: 300
	.days .day
		span
			display: inline-block
			width: 1.5rem
			text-align:center
		&.weekend
			opacity: 0.66
		font-size: 17px
		font-weight: 500
		letter-spacing: -1px

</style>